body{margin:0;background-color:#000}.star{position:absolute;width:1px;height:1px;background-color:#fff}.intro{position:absolute;top:30%;left:35%;z-index:1;animation:intro 6s ease-out 1s;color:#4bd5ee;font-weight:400;font-size:300%;opacity:0}@keyframes intro{0%{opacity:0}20%{opacity:1}90%{opacity:1}100%{opacity:0}}p{color:#ffff82}#board{font-family:Century Gothic,CenturyGothic,AppleGothic,sans-serif;transform:perspective(300px) rotateX(25deg);transform-origin:50% 100%;text-align:justify;position:absolute;margin-left:-9em;font-weight:bold;overflow:hidden;font-size:350%;height:50em;width:18em;bottom:0;left:50%}#board:after{position:absolute;content:" ";bottom:60%;left:0;right:0;top:0}#content{animation:scroll 120s linear 5s;position:absolute;top:100%}#title,#subtitle{text-align:center}@keyframes scroll{0%{top:100%}100%{top:-170%}}